@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300&family=Ubuntu+Mono&family=Ubuntu:wght@500&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300&family=Share+Tech+Mono&family=Ubuntu+Mono&family=Ubuntu:wght@500&display=swap");

@import "../../styles/vars/sizes";

.nav_main_wrapper {
    user-select: none;

    $nav_padding_bottom: 0;
    height: $nav_height - $nav_padding_bottom;

    padding-bottom: $nav_padding_bottom + px;

    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;

    backdrop-filter: saturate(100%) brightness(100%) blur(0px);
    box-shadow: 0 0px 0px 0px transparent;

    border-bottom: 1px solid rgba(0, 0, 0, 0.151);

    // ! .nav_active in './Navbar.scss'

    .nav_content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 100%;
        box-sizing: border-box;
        padding: 0 20px;
        padding-right: 10px;

        margin: 0 auto;
        max-width: $content-max-width;

        .logo_wrapper {
            width: 50px;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            margin-right: 20px;
        }
        $breakpoint: 739px;

        // .navigation,
        // .nav_content_right {
        //     height: $nav_height;
        // }

        .navigation {
            display: flex;
            align-items: center;
            justify-content: space-between;
            justify-self: center;
            padding: 0 25px;
            padding-right: 125px;

            @media screen and (max-width: 940px) {
                & {
                    padding-right: 54px;
                }
            }

            @media screen and (max-width: 940px) {
                & {
                    padding-right: 0;
                }
            }

            @media screen and (max-width: $breakpoint) {
                & {
                    padding: 0 0;
                }
            }

            @media screen and (max-width: 570px) {
                & {
                    display: none;
                }
            }

            .nav_link {
                margin: 0 15px;
                font-family: "Roboto Mono", monospace;
                font-family: "Ubuntu", sans-serif;
                font-family: "Ubuntu Mono", monospace;
                font-style: normal;
                font-weight: 400;
                font-size: 21px;
                font-weight: 600;
                text-align: center;
                letter-spacing: -0.005em;

                color: rgba(232, 232, 232, 0.918);

                &.active {
                    text-decoration: underline;
                    // background: red;
                }
            }
        }

        .nav_content_right {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .auth_links {
                display: flex;
                align-items: center;
                justify-content: space-between;

                margin-left: auto;

                box-sizing: border-box;
                padding: 0 20px;

                @media screen and (max-width: $breakpoint) {
                    & {
                        display: none;
                    }
                }

                .auth_link {
                    margin: 0 10px;
                    font-family: "Roboto Mono", monospace;
                    font-family: "Share Tech Mono", monospace;
                    font-family: "Ubuntu", sans-serif;
                    font-family: "Ubuntu Mono", monospace;
                    font-style: normal;
                    // font-weight: 400;
                    font-size: 17px;
                    // line-height: 28px;
                    text-align: center;
                    letter-spacing: -0.005em;

                    color: rgba(255, 255, 255, 0.849);

                    &.active {
                        text-decoration: underline;
                    }
                }
            }

            .nav_content_right__end {
                display: flex;
                align-items: center;
                justify-content: space-between;

                $item_width: 50px;

                .select_icon_wrapper {
                    // background: #000;
                    height: $item_width;
                    width: $item_width;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    &.change_language_svg_wrapper {
                        svg {
                            width: 31px;
                            height: 31px;

                            fill: white;
                        }
                    }

                    &.change_theme_svg_wrapper {
                        svg {
                            width: 30px;
                            height: 30px;
                            fill: white;
                        }
                    }
                }

                .burger_wrapper_main {
                    // background: #000;
                    box-sizing: border-box;
                    // padding: 0 10px 0 0px;
                    width: $item_width;
                    height: $item_width;
                    cursor: pointer;
                    // background: rgba(255, 0, 0, 0.486);
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    .burger_wrapper {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        // margin: 0 !important;
                        .burger {
                            height: 40px;
                            // margin-left: 5px;
                            display: flex;
                            justify-content: space-evenly;
                            align-items: center;
                            flex-direction: column;
                            .burger_line {
                                display: inline-block;
                                height: 2.7px;
                                width: 38px;
                                background: white;
                                flex-grow: 0;
                                flex-shrink: 0;
                            }
                        }
                    }
                }
            }
        }
    }
}
