.loader_wrapper {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.857);
    z-index: 20;
    box-sizing: border-box;
    padding-bottom: 20vh;
}

// scrollbar
$scrollbar_track_bg_color: #000000bd;
::-webkit-scrollbar {
    width: 12px;

    @media screen and (hover: none) {
        width: 2px;
    }
}
::-webkit-scrollbar-track {
    background: $scrollbar_track_bg_color;
}
::-webkit-scrollbar-thumb {
    background: linear-gradient(rgb(25, 126, 0), black, rgb(25, 126, 0));
    border: 1px solid $scrollbar_track_bg_color;
    border-radius: 2px;
    &:hover {
        background: linear-gradient(black, rgb(25, 126, 0), black);
    }
    &:active {
        background: linear-gradient(red, rgb(25, 126, 0), red);
    }
}

[data-title] {
    position: relative;

    $dt-top: 150%;
    $dt-left: -0px;

    &:hover::before,
    &.show_data_title::before {
        content: attr(data-title);
        position: absolute;
        background: #000;
        color: white;
        top: $dt-top;
        left: $dt-left;
        box-sizing: border-box;
        padding: 5px;
        font-size: 20px;
        white-space: nowrap;
    }

    &:hover::after,
    &.show_data_title::after {
        content: "";
        position: absolute;
        left: $dt-left + 8;
        bottom: -($dt-top - 100);
        display: inline-block;
        border: 8px solid transparent;
        border-bottom: 8px solid black;
    }

    &:hover::before,
    &.show_data_title::before,
    &:hover::after,
    &.show_data_title::after {
        z-index: 20;
        transform: translateZ(20px);
    }

    @media (hover: none) {
        &:hover::before,
        &:hover::after {
            display: none;
        }
    }
}

