.sidebarMenu_mainWrapper {
    user-select: none;
    width: 100vw;
    height: 100vh;

    display: flex;

    .sidebarMenu_mainWrapper_computer {
        flex-direction: row-reverse;
    }

    .area_to_close {
        height: 100vh;
        width: 22vw;
        flex-grow: 1;
    }

    .content {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
        width: 78vw;
        box-shadow: 0 0 20px 2px rgba(0, 0, 0, 0.575);
        backdrop-filter: blur(2px);

        @media screen and (min-width: 526px) {
            & {
                width: 440px;
            }
        }

        .top {
            width: 100%;
            margin-bottom: 2px;
            height: 150px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            //
            .top_main__user_is_not_authenticated {
                width: 100%;
                height: 100%;
                box-sizing: border-box;
                padding: 20px;

                display: flex;
                flex-direction: column;
                align-items: center;

                .auth_btn {
                    width: 90%;
                    height: 45%;
                    margin: 5px 0;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    text-transform: uppercase;
                    border-radius: 5px;
                }
            }
            //
            .top_main__user_is_authenticated {
                display: flex;
                align-items: center;
                justify-content: space-between;
                width: 100%;
                height: 100%;

                .left {
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-end;
                    align-items: flex-start;
                    height: 100%;
                    box-sizing: border-box;
                    padding-left: 15px;

                    .photo_wrapper_main {
                        height: 80%;
                        display: flex;
                        align-items: center;
                        .photo_wrapper {
                            height: 80px;
                            width: 80px;
                            border-radius: 50%;
                            overflow: hidden;
                            .photo {
                                width: 100%;
                                height: 100%;
                                // background: red;
                                color: orange;
                                font-size: 20px;
                                // &[alt] {
                                //     display: block;
                                //     text-align: center;
                                //     vertical-align: middle;
                                //     background: red;
                                //     width: 100px;
                                //     height: 100px;
                                // }
                            }
                        }
                    }
                    .username {
                        height: 20%;
                        display: inline-block;
                        display: flex;
                        align-items: center;
                        font-size: 100%;
                    }
                }
                .right {
                    width: 20%;
                    height: 100%;
                    box-sizing: border-box;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start;
                    align-items: flex-end;
                    .settings {
                        width: 50px;
                        height: 50px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        margin: 5px;
                        border-radius: 10%;
                    }
                }
            }
            //
        }
        .main {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .nav_links_wrapper {
                display: flex;
                flex-direction: column;
                width: 100%;
                .navLink {
                    box-sizing: border-box;
                    padding: 10px 0;
                    margin: 2px 0;
                    padding-left: 20px;
                    &.active {
                        //
                    }
                }
            }
        }
        .bottom {
            display: flex;
            padding: 2px 0;
            .admin_link {
                width: 100%;
                box-sizing: border-box;
                padding: 5px 0;
                padding-left: 20px;
            }
        }
    }
}
