.page {
    top: 0;
    left: 0;
    // position: absolute;
}
.page-enter-active {
    animation: fade-in 1s forwards;
}
.page-enter {
    opacity: 0;
    transform: scale(1.1);
}
.page-exit-active {
    animation: fade-out 1s forwards;
    z-index: 20;
}
.page-exit {
    opacity: 1;
    transform: scale(1);
}
@keyframes fade-in {
    0% {
        opacity: 0;
        transform: translateX(0);
    }
    100% {
        opacity: 0;
        transform: translateX(400px);
    }
}
@keyframes fade-out {
    0% {
        opacity: 1;
        transform: translateX(0);
    }
    100% {
        // opacity: 0;
        transform: translateX(100%);
    }
}
