.main {
    display: flex;
    width: fit-content;
    z-index: 0;

    .header {
        z-index: 0;

        .header_content {
            position: relative;
            display: flex;
            justify-content: center;
            align-items: center;

            cursor: pointer;

            .header_title {
                background: orange;
                box-sizing: border-box;
                padding: 5px;
            }

            // OPTIONS
        }
    }
}

.main_active {
    z-index: 10;
    .back_screen {
        height: 100vh;
        width: 100vw;
        position: fixed;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.479);
        z-index: 3;
    }
    .header {
        z-index: 5;
    }
}

.options {
    background: #000;
    color: white;

    position: absolute;
    cursor: default;

    display: flex;
    flex-direction: column;

    top: 100%;
    right: 0;

    border-radius: 5px;
    z-index: 10;

    .option {
        box-sizing: border-box;
        padding: 10px 20px;
        margin: 5px;
        background: rgb(0, 0, 0);
        border-radius: 1px;
        color: white;
        box-sizing: border-box;
        border: 1px solid white;

        display: inline-flex;
        justify-content: center;
        align-items: center;
        &.option_active {
            background: rgb(0, 175, 0);
            color: black;
        }
    }
}

@media screen and (max-width: 400px) {
    .options {
        position: fixed;
        left: 50%;
        right: unset;
        top: 80px;
        transform: translateX(-50%);
        .option {
            font-size: 18px;
        }
    }
}
