@mixin nav {
    .nav {
        background-color: transparent;

        .nav_icon {
            fill: rgb(216, 216, 216);
            &:hover {
                fill: rgb(128, 128, 128);
            }
            &:active {
                fill: rgb(189, 3, 3);
            }
        }
        .burger {
            fill: rgb(255, 255, 255);
        }
    }
}

@mixin sidemenu {
    .sidemenu {
        background-color: rgb(85, 0, 0);
        .top {
            background-color: rgb(89, 0, 0);
            .auth_btn {
                background-color: green;
                &:hover {
                    background-color: rgb(0, 106, 0);
                }
                &:active {
                    background-color: rgb(0, 82, 0);
                }
            }
            .settings {
                background-color: black;
            }
        }
        .navLink {
            background-color: rgb(57, 0, 0);
            color: rgb(195, 195, 195);
            &:hover {
                background-color: rgb(130, 0, 0);
            }
            &:active {
                background-color: rgb(106, 0, 0);
            }
            &.active {
                background-color: rgb(130, 0, 0);
                &:hover {
                    background-color: rgba(94, 0, 0, 0.854);
                }
                &:active {
                    background-color: rgba(61, 0, 0, 0.854);
                }
            }
        }
        .bottom {
            background-color: rgba(255, 0, 0, 0.83);
            .admin_link {
                background-color: rgba(10, 10, 10, 0.768);
                color: rgb(125, 235, 0);
            }
        }
    }
}

@mixin footer {
    .footer {
        background-color: rgb(165, 0, 0);
    }
}

@mixin form {
    .form {
        background-color: linear-gradient(170deg, rgba(0, 41, 145, 0.9), rgba(255, 0, 0, 0.551));

        box-shadow: 0 0 4px 3px rgba(45, 0, 0, 0.646);

        animation: form_animation;
        animation-duration: 4s;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
        animation-direction: alternate-reverse;

        @keyframes form_animation {
            0% {
                background: linear-gradient(170deg, rgba(0, 41, 145, 0.674), rgba(255, 0, 0, 0.551));
                background: red;
            }
            50% {
                background: linear-gradient(170deg, rgba(255, 0, 0, 0.551), rgba(0, 41, 145, 0.9));
            }
            100% {
                background: linear-gradient(170deg, rgba(0, 41, 145, 0.674), rgba(255, 0, 0, 0.551));
            }
        }
    }
    //
    .errorMessage {
        border: 2px solid rgb(170, 0, 0);
        color: rgb(145, 145, 145);
        text-shadow: 0 0 5px rgb(68, 146, 139);
        box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.872);
    }
    //
    .input_main_wrapper {
        .label {
            color: rgb(207, 207, 207);
        }
        .input:focus + .label, // TODO
        .label_active {
            background-color: rgba(159, 159, 159, 0.612);
            color: rgb(7, 0, 0);
            box-shadow: 0 0 6px 2px black;
        }
        .input {
            box-shadow: 0 0 5px 5px blue;
            background-color: rgba(48, 0, 171, 0.489);
            &:hover {
                background-color: rgba(35, 0, 123, 0.436);
            }
            &:active {
                background-color: rgba(48, 0, 171, 0.626);
                box-shadow: 0 0 10px 5px rgb(18, 18, 250);
            }
            &:focus {
                background-color: rgba(48, 0, 171, 0.626);
            }
            &.has_errors {
                background-color: #df0000cf;
                box-shadow: 0 0 10px 5px rgb(60, 0, 0);
            }
            &.correct {
                background-color: #009900cc;
                box-shadow: 0 0 10px 5px rgb(1, 28, 0);
            }
        }
        .red_message {
            color: #0c0b0b;
            text-shadow: 0 0 3px #000000bd;
        }
    }

    .ftp {
        color: rgb(181, 181, 181);
    }
    //
    .button {
        background-color: red;

        &:focus {
            background-color: rgb(186, 0, 0);
        }

        &:active &:disabled {
            background-color: green;
        }
        &:enabled {
            &:hover {
                background-color: orange;
            }
            &:active {
                background-color: rgb(167, 109, 0);
                // box-shadow: 0 0 5px 10px red;
            }
        }
    }
}

@mixin auth_pages {
    .auth_page_name {
        color: #edededc9;
    }
    //
    .help_link {
        color: rgba(0, 136, 233, 0.614);

        &:hover {
            color: rgba(0, 136, 233, 0.737);
        }
        &:active {
            color: rgba(0, 77, 133, 0.792);
        }
    }
    //
    .link_to_another_auth_page {
        color: red;
    }
}

body[data-theme="RED"] {
    background-color: #590000;

    *::selection {
        background: rgb(17, 167, 0);
    }

    //
    @include nav;
    //
    @include sidemenu;
    //
    @include footer;
    //
    @include form;
    //
    @include auth_pages;
}
