@import "../../MessageForm.module";

.message_textarea_wrapper {
    min-height: $min-height;

    display: flex;
    align-items: center;

    flex-grow: 1;

    .message_input {
        background: transparent;
        resize: none;
        outline: none;
        border: none;
        height: 100%;
        width: 100%;
        overflow: hidden;
        overflow-y: scroll;
        box-sizing: border-box;
        padding: 10px 0;
        padding-left: 20px;
        font-size: 16px;

        max-height: 160px !important;

        &::-webkit-scrollbar {
            width: 0;
        }
    }
}
