.intro_photos_wrapper {
    width: 100%;
    height: 100%;

    display: block;

    position: absolute;
    top: 0;
    left: 0;

    .intro_photos_face {
        width: 100%;
        height: 100%;

        display: block;

        background-color: black;
        background-image: url("./_images/face.jpg");
        background-size: cover;
        background-repeat: no-repeat;
        background-position: 50% 70%;
        background-attachment: fixed;

        filter: saturate(100%) sepia(100%) brightness(50%);

        opacity: 0.8;

        transform: translateZ(5px);

        // box-shadow: 0 0 8px 8px white inset;

        @media screen and (max-width: 770px) {
            background-position: 50% 100%;
        }
    }
}

.canvasWrapper {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0.5; //!
    // transform: translateZ(50px) scale(1.2);

    .canvas {
        width: 100%;
        height: 100%;
        display: block;
    }
}

.intro_screen_content_wrapper {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    // transform: translateZ(70px);

    box-sizing: border-box;
    padding-top: $nav_height;

    .intro_screen_content {
        height: 100%;
        width: 100%;
        box-sizing: border-box;
        padding: 20px;
        padding-bottom: 40px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: center;

        .header1 {
            color: white;
            text-shadow: 0 0 20px black;
            text-align: center;

            text-transform: capitalize;
        }
        .header2 {
            color: rgba(255, 255, 255, 0.692);
            text-align: center;
            margin: 5px 0;
            margin-bottom: 7.5px;

            // animation: name duration timing-function delay iteration-count direction fill-mode;
            animation-name: header2;
            animation-duration: 1200ms;
            animation-iteration-count: 1;

            @keyframes header2 {
                0% {
                    color: transparent;
                    transform: scale(0.5);
                }
                100% {
                    transform: scale(1);
                }
            }
        }

        .scrollDownBtn {
            outline: none;
            border: none;
            user-select: none;
            display: inline-flex;
            max-width: 90%;
            box-sizing: border-box;
            padding: 20px 40px;
            background: rgb(0, 0, 0);
            color: rgb(0, 117, 0);
            border-radius: 10px;
            margin-top: 1px;
            border: 0.1px solid rgba(255, 255, 255, 0.24);

            text-transform: uppercase;

            &:hover {
                transform: scale(1.1);
                background: #000;
                box-shadow: 0 0 5px 2px rgba(0, 0, 0, 0.726);
            }
            &:active {
                background: rgb(0, 0, 0);

                transform: scale(0.9);
            }
        }
    }
}
