@import "../../../../styles/vars/sizes";

.main_wrapper {
    width: 100%;
    background: rgb(155, 0, 116);
    box-sizing: border-box;

    max-width: $chat-max-width;
    transition: all 500ms;

    &.active {
        box-shadow: 0 50px 50px 20px rgba(0, 0, 0, 0.835);

        .content_wrapper {
            height: 50vh;
            background: rgba(78, 0, 0, 0.479);
        }
    }

    .content_wrapper {
        padding: 5px 15px;
        transition: all 500ms;

        height: 30px;

        .content {
            display: flex;
            align-items: center;
            .content_top {
            }
        }
    }

    .notConncetedMessageWrapper {
        box-sizing: border-box;
        padding: 0 20px;
        background: red;
        border-bottom: 1px solid rgb(153, 0, 0);
        .notConncetedMessageContent {
            display: flex;
            justify-content: space-between;
            align-items: center;
            .notConncetedMessageText {
                font-size: 18px;
                &:after {
                    content: "";

                    animation-name: text_dots_animation;
                    animation-iteration-count: infinite;
                    animation-duration: 1s;

                    @keyframes text_dots_animation {
                        0% {
                            content: "";
                        }
                        25% {
                            content: ".";
                        }
                        50% {
                            content: "..";
                        }
                        70% {
                            content: "...";
                        }
                    }
                }
            }
            .notConncetedMessageSpinnerWrapper {
                display: inline-flex;
                justify-content: center;
                align-items: center;
                .notConncetedMessageSpinner {
                    $size: 10px;
                    height: $size;
                    width: $size;
                    border-radius: 50%;
                    border-width: 1px;
                    border-style: solid;
                    border-color: #000;
                    border-top-color: red;

                    animation-name: spinning;
                    animation-iteration-count: infinite;
                    animation-duration: 1s;
                    animation-timing-function: linear;
                    transform: rotate(0deg);

                    @keyframes spinning {
                        from {
                            transform: rotate(0deg);
                        }
                        to {
                            transform: rotate(360deg);
                        }
                    }
                }
            }
        }
    }
    .conncetedMessage {
        display: block;
        width: 100%;
        height: 5px;
        background: green;
    }
}
