@import "../../styles/vars/sizes";

.main_wrapper {
    background: #000;
    // height: 100vh;
    box-sizing: border-box;
    // padding-bottom: 61px;

    background: rgb(200, 200, 200);

    position: fixed;
    top: $nav_height;
    bottom: 0;
    left: 0;
    right: 0;

    .main_content_wrapper {
        height: 100%;
        position: relative;
        margin: 0 auto;

        .main_content {
            height: 100%;
            box-sizing: border-box;

            // position: relative;

            .main {
                height: 100%;

                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: space-between;

                .messageFormWrapper {
                    width: 100%;
                    max-width: $chat-max-width;
                    z-index: 5000;
                }
            }
            //
        }
    }
}
