.loader_wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    padding: 20px;
}
.loaderContent {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.loader {
    width: 50px;
    height: 50px;
    animation-name: loaderSpinning;
    animation-duration: 1500ms;
    animation-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
    animation-iteration-count: infinite;
    animation-direction: normal;
    animation-delay: 0;
    transform: rotate(0);
    border-radius: 0;
    background: rgb(0, 160, 177);
}
@keyframes loaderSpinning {
    from {
        transform: rotate(0);
        background: rgb(0, 64, 174);
        border-radius: 50%;
    }
    50% {
        border-radius: 0%;
        box-shadow: 0 0 20px 2px ;
    }
    to {
        transform: rotate(360deg);
        border-radius: 50%;
    }
}