$min-height: 40px;
.formWrapper {
    width: 100%;
    background: rgb(221, 221, 221);
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    z-index: 50;

    min-height: $min-height;

    box-sizing: border-box;
    padding: 5px 10px;

    .smiles_button_wrapper {
        background: red;
        width: 40px;
        height: 40px;
        display: flex;
        justify-content: center;
        flex-shrink: 0;
        align-items: center;
    }

    .button,
    .openSendFile {
        height: $min-height;
        width: $min-height;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        background: rgb(110, 0, 0);
        .openSendFileSvg {
            fill: white;
            height: 50%;
        }
    }

    .button {
        outline: none;
        border: none;
        background: blue;
        .SendMessageSvg {
            fill: white;
        }
    }
}
