.skills_wrapper_main {
    $skill_wrapper_margin: 5px;

    display: flex;
    flex-wrap: wrap;
    max-width: 500px;
    margin: -$skill_wrapper_margin;
    .skill_wrapper {
        margin: $skill_wrapper_margin;

        &,
        .skill_name_wrapper,
        .skill_name,
        .skill_description_wrapper {
            border-radius: 5px;
        }
        // overflow: hidden;

        .skill_name_wrapper {
            display: flex;
            justify-content: space-between;
            align-items: center;

            .skill_name {
                display: inline-block;
                background: green;
                padding: 10px;
                white-space: nowrap;
                user-select: none;
                cursor: help;
                user-select: none;

                &:hover {
                    background: rgb(0, 109, 0);
                }
                &:active {
                    background: rgb(0, 139, 0);
                }
            }
            .close_btn {
                display: none;
            }
        }
        .skill_description_wrapper {
            display: none;
        }

        &.skill_description_is_opened {
            display: block;
            width: 100%;
            background: yellow;

            border-top-left-radius: 10px;

            .skill_name {
                // border-radius: 0px;
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
                border-bottom-left-radius: 0;

                &,
                &:hover,
                &:active {
                    cursor: default;
                    background: rgb(10, 78, 0);
                }
            }

            .close_btn {
                display: inline-flex;
                justify-content: center;
                align-items: center;
                box-sizing: border-box;
                height: 25px;
                width: 25px;
                margin: 6px;
                background: red;
                user-select: none;
                cursor: pointer;
            }

            .skill_description_wrapper {
                display: block;
                width: 100%;
                background: #ffa800d1;
                box-sizing: border-box;
                padding: 10px;
                border-top-left-radius: 0;

                border-top-right-radius: 0;

                .skill_description_text {
                }
            }
        }
    }

    // animation
    .skill_animated {
        visibility: hidden;
    }
    //
    .skill_animated_enter {
        visibility: hidden;
    }
    .skill_animated_enter_active {
        opacity: 0.2;

        transform: scale(0.2);

        // transform: scale(1);
        // opacity: 1;
    }

    .skill_animated_enter_done {
        opacity: 1;
        visibility: visible;

        transition: all 40ms linear;

        // transition: all 4000ms ease-in;
    }
    //
    // .skill_animated_exit {
    //     opacity: 1;
    // }
    // .skill_animated_exit_active {
    //     opacity: 0;
    //     transition: opacity 500ms ease-in;
    // }
    // animation end
}
