.content_wrapper {
    transform-style: preserve-3d;
    transform: translateZ(500px);

    //
    margin-top: calc(100vh - $nav_height);
}
// ! parallax end

.content_wrapper {
    box-shadow: 0 -20px 20px 2px rgba(0, 0, 0, 0.596);

    background: rgb(0, 104, 61);

    box-sizing: border-box;
    padding: 0px 8px;
    padding-top: $nav_height;

    .content_top_line {
        margin-bottom: 5px;
        display: block;
        background: #000;
        height: 5px;
        width: 50%;
        max-width: 380px;
        border-radius: 10px;
    }
}

// // TODO DELETE
.header {
    margin-top: 20px;
    background: #9300a38e;
    p,
    h3 {
        box-sizing: border-box;
        padding: 20px;
    }
    h3 {
        padding-top: 20px;
        padding-bottom: 0;
    }
    p,
    h3 {
        transform: translateX(-100%) scale(0.5);
        transition: all 0.8s;
    }
    &.active {
        * {
            transform: translateX(0%) scale(1);
        }
    }
}
