.main_wrapper {
    width: 100%;
    display: flex;
    //
    &.outgoing {
        justify-content: flex-end;

        .content_wrapper {
            border-bottom-right-radius: 4px;
            &,
            &:before {
                background: rgb(44, 139, 0);
            }
            &:before {
                right: -5px;

                border-top-right-radius: 71px;
            }
        }
    }
    //
    &.incoming {
        justify-content: flex-start;

        .content_wrapper {
            background: rgb(170, 175, 168);
            border-bottom-left-radius: 4px;
            &,
            &:before {
                background: rgb(170, 175, 168);
            }
            &:before {
                left: -5px;

                border-top-left-radius: 71px;
            }
        }
    }
    //
    .content_wrapper {
        box-sizing: border-box;
        padding: 19px;
        padding-bottom: 17px;
        margin: 5px;
        border-radius: 20px;
        max-width: 80%;
        position: relative;

        // transform: translateX(0%);

        &:before {
            content: "";
            display: block;
            position: absolute;
            height: 10px;
            width: 10px;
            bottom: 0;
        }

        .content {
            width: 100%;
            .username {
                color: rgb(0, 0, 195);
                font-weight: 600;
            }
            .message_text {
                &,
                * {
                    // nullification
                    appearance: none;
                    margin: unset;
                    padding: unset;
                    font: unset;
                    background: unset;
                    color: unset;

                    word-break: break-word;
                    white-space: pre-wrap;
                    display: flow-root;
                    unicode-bidi: plaintext;

                    font-size: 16px;

                    display: inline;
                }

                a {
                    color: blue;
                    &:hover {
                        color: rgb(28, 28, 255);
                    }
                    &:active {
                        color: rgb(0, 0, 179);
                    }
                }
                b {
                    font-weight: 900;
                }
                i {
                    font-style: italic;
                }
                del {
                }
                em {
                    font-style: normal;
                    text-decoration: underline;
                }
                mark {
                    background: yellow;
                }
                ins {
                }
                sub {
                }
                code {
                    // font-family: "Courier New", Courier, monospace;
                    letter-spacing: 2px;
                    cursor: pointer;
                }

                // TODO
                // ?
                dfn {
                    background: rgba(110, 110, 110, 0.876);
                    color: transparent;
                    &:hover {
                        background: unset;
                        color: unset;
                    }
                }
            }
            .message_info {
                float: right;
                display: flex;
                align-items: center;
                margin: 7px;
                margin-bottom: 0;
                margin-right: 0;
                .loading {
                    display: inline-flex;
                    height: 10px;
                    width: 10px;
                    // background: red;
                    border-radius: 50%;
                    border-width: 2px;
                    border-style: solid;
                    border-color: black;
                    border-bottom-color: transparent;

                    transform: rotate(0deg);

                    animation-name: loader_spinning;
                    animation-duration: 400ms;
                    animation-timing-function: ease-in;
                    animation-iteration-count: infinite;

                    @keyframes loader_spinning {
                        100% {
                            transform: rotate(360deg);
                        }
                    }
                }
                .time {
                    font-weight: 550;
                    color: rgba(0, 0, 0, 0.808);
                    margin-bottom: 0;
                    margin-right: 0;
                }
                .delivery_info {
                    margin-left: 4.5px;
                    display: flex;
                    align-items: center;
                }
            }
        }
    }
}

.a_message_enter {
    transform: translate(0%, 100%) scale(0.5);
}
.a_message_enter_active {
    transition: all 0.2s;
    transform: translateY(0%);
}
.a_message_enter_done {
    transition: all 0.2s;
    transform: translate(0%);
}
