@import "../../../../styles/vars/sizes.scss";

.messages_wrapper {
    width: 100%;
    box-sizing: border-box;

    overflow: hidden;
    overflow-y: scroll;
    flex-grow: 1;

    padding-top: 8px;
    position: relative;

    .messages_content {
        max-width: $chat-max-width;
        margin: 0 auto;
        box-sizing: border-box;
        height: 100%;

        display: flex;
        flex-direction: column;
        // justify-content: flex-end;

        .messages_date_group {
            margin-top: auto;

            .sticky_date_wrapper {
                position: sticky;
                top: 0;
                width: 100%;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 4px auto;
                z-index: 21;
                .sticky_date {
                    color: white;
                    background: rgb(0, 37, 139);
                    box-sizing: border-box;
                    padding: 8px 14px;
                    border-radius: 20px;
                    font-size: 15.2px;
                    opacity: 0.9;
                }
            }
        }
        .no_messages_yet_text_wrapper {
            width: 100%;
            display: flex;
            justify-content: center;
            .no_messages_yet_text {
                color: rgb(22, 0, 56);
                font-size: 20px;
            }
        }
    }
}

.scrollDownBtn {
    $scrollDownSize: 55px;
    width: $scrollDownSize;
    height: $scrollDownSize;

    position: fixed;
    right: 30px;
    transform: translateY(-100%) scale(0.2);

    border-radius: 50%;
    overflow: hidden;
    justify-content: center;
    align-items: center;

    display: flex;

    opacity: 0;
    visibility: hidden;

    transition: all 1s;

    bottom: -90px;

    &:hover {
        // background: #000;

        transform: translateY(-100%) scale(0.8);
        // TODO animation
    }
    &:active {
        // background: red;
        transform: translateY(-100%) scale(0.5);
    }
    &.scrollDownBtnVisible {
        // display: flex;
        opacity: 1;

        visibility: visible;

        transform: translateY(-100%) scale(1);

        bottom: 40px;

        background: rgb(21, 180, 0);
    }
}

.lastChildWrapper {
    // background: #000;
    // position: absolute;
    // top: 0;
    .lastChild {
        display: inline-block;
        height: 10px;
        width: 100%;
        background: rgba(255, 0, 0, 0);
    }
}
