.chatLinkWrapper {
    display: flex;
    justify-content: center;
    align-items: center;

    $size: 65px;

    height: $size;
    width: $size;
    position: fixed;
    right: 0;
    margin: 30px;
    bottom: -100%;

    border-radius: 50%;
    background: red;

    transition: 0.6s;

    &.chatLinkActive {
        bottom: 0;
        background: green;
    }

    .chatLinkSvg {
        height: 42%;
        fill: #e6e6e6;
    }
}
